<template>
  <div>
    <div style="margin-bottom: 10px">
      <el-input v-model="params.name" style="width: 200px" placeholder=""></el-input>
      <el-input v-model="params.phone" style="width: 200px; margin-left: 5px" placeholder=""></el-input>
      <el-button round type="warning" style="margin-left: 10px" @click="findBySearch()"></el-button>
      <el-button round type="warning" style="margin-left: 10px" @click="reset()"></el-button>
      <el-button round type="primary" style="margin-left: 10px" @click="add()" icon="el-icon-plus"></el-button>
    </div>
    <div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="name" label="" width="90"></el-table-column>
        <el-table-column prop="sex" label="" width="90"></el-table-column>
        <el-table-column prop="age" label="" width="90"></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="edit(scope.row)" icon="el-icon-edit"></el-button>
            <el-popconfirm title="" @confirm="del(scope.row.id)">
              <el-button slot="reference" type="danger" style="margin-left: 5px" icon="el-icon-delete"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="margin-top: 10px">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[5, 10]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
    </div>
    <div>
      <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
          <el-form :model="form">
            <el-form-item label="" label-width="15%">
              <el-input v-model="form.name" autocomplete="off" style="width: 90%"></el-input>
            </el-form-item>
            <el-form-item label="" label-width="15%">
              <el-radio v-model="form.sex" label=""></el-radio>
              <el-radio v-model="form.sex" label=""></el-radio>
            </el-form-item>
            <el-form-item label="" label-width="15%">
              <el-input v-model="form.age" autocomplete="off" style="width: 90%"></el-input>
            </el-form-item>
            <el-form-item label="" label-width="15%">
              <el-input v-model="form.phone" autocomplete="off" style="width: 90%"></el-input>
            </el-form-item>
            <el-form-item label="" label-width="15%">
              <el-input v-model="form.address" autocomplete="off" style="width: 90%"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="submit()"> </el-button>
          </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";
  export default {
    name: "AdminView",
    data() {
      return {
        params: {
          name: '',
          phone: '',
          pageNum: 1,
          pageSize: 5
        },
        tableData: [],
        total: 0,
        dialogFormVisible: false,
        form: {}
      }
    },
    created() {
      this.load();
      this.findBySearch();
    },
    methods: {
      load() {
        request.get("/admin").then(res => {
          if (res.code === '0') {
            this.tableData = res.data;
          }
        })
      },
      findBySearch() {
        request.get("/admin/search", {
          params: this.params
        }).then(res => {
          if (res.code === '0') {
            this.tableData = res.data.list;
            this.total = res.data.total;
          } else {
            this.$message({
              message: res.msg,
              type: 'success'
          });
          }
        })
      },
      reset(){
        this.params = {
            pageNum: 1,
            pageSize: 5,
            name: '',
            phone: ''
        }
        this.findBySearch();
      },
      handleSizeChange(pageSize) {
        this.params.pageSize = pageSize;
        this.findBySearch();
      },
      handleCurrentChange(pageNum) {
        this.params.pageNum = pageNum;
        this.findBySearch();
      },
      add(){
        this.form = {};
        this.dialogFormVisible = true;
      },
      edit(obj){
        this.form = obj;
        this.dialogFormVisible = true;
      },
      submit(){
        request.post("/admin", this.form).then(res => {
          if(res.code === '0'){
            this.$message({
              message: '!',
              type: 'success'
            });
            this.dialogFormVisible = false;
            this.findBySearch();
          }else{
            this.$message({
              message: res.msg,
              type: 'success'
            });
          }
        })
      },
      del(id) {
        request.delete("/admin/" + id).then(res => {
          if (res.code === '0') {
            this.$message({
              message: '!',
              type: 'success'
            });
          this.findBySearch();
          } else {
          this.$message({
            message: res.msg,
            type: 'success'
            });
          }
        })
      }
    }
  }
</script>